import React, { Component } from 'react'
import { Link } from "react-router-dom";


class navbar extends Component {
 
render() {

return (
<nav className="navbar navbar-expand-lg navbar-light bg-transparent p-4">
<div className="container">

  <Link  className="navbar-brand text-dark font-weight-bold" to={'/'}>
        PL <span className="sr-only">(current)</span>
  </Link>

{ window.location.pathname === "/" &&
  <button className="navbar-toggler border-0 btn btn-link" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
}
  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto font-weight-bold text-uppercase small">

     <li className="nav-item">
        { window.location.pathname === "/" &&
        <a className="nav-link text-dark font-weight-bold" href="#Projects">Projects</a>
          }
      </li>
      <li className="nav-item font-weight-bold">
       { window.location.pathname === "/" &&
        <a className="nav-link text-dark" href="#TechStack">TechStack</a>
         }
      </li> 

    </ul>
  </div>

   { window.location.pathname === "/Portfolio" &&
          <Link className="nav-link text-dark btn btn-link" to={'/'}>Go Back</Link>
      }

  </div>
</nav>

        )
    }
}

export default navbar;